module.exports = "## Bar\r\n\r\n柱状图。\r\n\r\n## 使用\r\n\r\n```html\r\n<m-bar id=\"myBar\" width=\"600\" height=\"300\" data=\"{\r\n  labels: ['Red', 'Blue', 'Yellow', 'Green', 'Purple', 'Orange'],\r\n  datasets: [{\r\n    label: '# of Votes',\r\n    data: [12, 19, 3, 5, 2, 3],\r\n    backgroundColor: [\r\n      'rgba(255, 99, 132, 0.2)',\r\n      'rgba(54, 162, 235, 0.2)',\r\n      'rgba(255, 206, 86, 0.2)',\r\n      'rgba(75, 192, 192, 0.2)',\r\n      'rgba(153, 102, 255, 0.2)',\r\n      'rgba(255, 159, 64, 0.2)'\r\n    ],\r\n    borderColor: [\r\n      'rgba(255,99,132,1)',\r\n      'rgba(54, 162, 235, 1)',\r\n      'rgba(255, 206, 86, 1)',\r\n      'rgba(75, 192, 192, 1)',\r\n      'rgba(153, 102, 255, 1)',\r\n      'rgba(255, 159, 64, 1)'\r\n    ],\r\n    borderWidth: 1\r\n  }]\r\n}\"\r\n  options=\"{\r\n  legend: {\r\n    display: false\r\n  },\r\n  scales: {\r\n    yAxes: [{\r\n      ticks: {\r\n        beginAtZero: true\r\n      }\r\n    }]\r\n  }\r\n}\"></m-bar>\r\n```\r\n\r\n## 更新图表\r\n\r\n```js\r\n//获取该元素\r\nvar chart = document.querySelector('#myBar')\r\n//更改图表的数据\r\nchart.props.data.datasets.forEach(dataset => {\r\n  dataset.data.forEach((item, index) => {\r\n    dataset.data[index] = Math.random() * 100\r\n  })\r\n})\r\n//更新图表视图\r\nchart.update()\r\n```\r\n\r\n## API\r\n\r\n### Props\r\n\r\n```jsx\r\n{\r\n  data: object,\r\n  options: object,\r\n  width: number,\r\n  height: number,\r\n  horizontal?: string\r\n}\r\n```"

